import {Resource} from 'dynamic-acl';

class Permission {
	/**
	 *
	 * @param {Acl} acl
	 * @param {string} name
	 * @param {Array.<string>} privileges
	 */
	constructor(acl, name, privileges) {
		this.acl = acl;
		this.name = name;
		this.privileges;
		this.acl.addResource(new Resource(name, privileges));
	}

	build() {

	}

	allow(roleId, privilege, condition) {
		this.acl.allow(roleId, this.name, privilege, condition);
	}

	deny(roleId, privilege, condition) {
		this.acl.deny(roleId, this.name, privilege, condition);
	}
}
export default Permission;
